import { apiClient } from './client';

// –¢–∏–ø—ã –¥–ª—è –∫–æ—Ä–∑–∏–Ω—ã
export interface CartItem {
  cart_item_id: number;
  item_id: number;
  item_name: string;
  item_description: string;
  unit_price: number;
  quantity: number;
  total_price: number;
  added_at: string;
  image_url: string | null;
  category_name: string;
  is_vegetarian: boolean;
  is_spicy: boolean;
}

export interface Cart {
  cart_id: string;
  user_id: string;
  items: CartItem[];
  total_items: number;
  total_amount: number;
  created_at: string;
  updated_at: string;
}

export interface AddToCartRequest {
  item_id: number;
  quantity: number;
}

export interface UpdateCartItemRequest {
  quantity: number;
}

export class CartService {
  private static instance: CartService;
  
  static getInstance(): CartService {
    if (!CartService.instance) {
      CartService.instance = new CartService();
    }
    return CartService.instance;
  }

  /**
   * –ü–æ–ª—É—á–∏—Ç—å –∫–æ—Ä–∑–∏–Ω—É –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è
   */
  async getCart(): Promise<Cart> {
    try {
      const response = await apiClient.get('/cart');
      return response.data;
    } catch (error: any) {
      throw new Error(error.response?.data?.message || '–û—à–∏–±–∫–∞ –ø–æ–ª—É—á–µ–Ω–∏—è –∫–æ—Ä–∑–∏–Ω—ã');
    }
  }

  /**
   * –î–æ–±–∞–≤–∏—Ç—å —Ç–æ–≤–∞—Ä –≤ –∫–æ—Ä–∑–∏–Ω—É
   */
  async addToCart(itemId: number, quantity: number = 1): Promise<Cart> {
    try {
      const response = await apiClient.post('/cart/add', {
        item_id: itemId,
        quantity,
      });
      return response.data;
    } catch (error: any) {
      throw new Error(error.response?.data?.message || '–û—à–∏–±–∫–∞ –¥–æ–±–∞–≤–ª–µ–Ω–∏—è —Ç–æ–≤–∞—Ä–∞ –≤ –∫–æ—Ä–∑–∏–Ω—É');
    }
  }

  /**
   * –û–±–Ω–æ–≤–∏—Ç—å –∫–æ–ª–∏—á–µ—Å—Ç–≤–æ —Ç–æ–≤–∞—Ä–∞ –≤ –∫–æ—Ä–∑–∏–Ω–µ
   */
  async updateCartItem(itemId: number, quantity: number): Promise<Cart> {
    try {
      const response = await apiClient.put(`/cart/item/${itemId}`, {
        quantity,
      });
      return response.data;
    } catch (error: any) {
      throw new Error(error.response?.data?.message || '–û—à–∏–±–∫–∞ –æ–±–Ω–æ–≤–ª–µ–Ω–∏—è –∫–æ–ª–∏—á–µ—Å—Ç–≤–∞ —Ç–æ–≤–∞—Ä–∞');
    }
  }

  /**
   * –£–¥–∞–ª–∏—Ç—å —Ç–æ–≤–∞—Ä –∏–∑ –∫–æ—Ä–∑–∏–Ω—ã
   */
  async removeFromCart(itemId: number): Promise<Cart> {
    try {
      const response = await apiClient.delete(`/cart/item/${itemId}`);
      return response.data;
    } catch (error: any) {
      throw new Error(error.response?.data?.message || '–û—à–∏–±–∫–∞ —É–¥–∞–ª–µ–Ω–∏—è —Ç–æ–≤–∞—Ä–∞ –∏–∑ –∫–æ—Ä–∑–∏–Ω—ã');
    }
  }

  /**
   * –û—á–∏—Å—Ç–∏—Ç—å –∫–æ—Ä–∑–∏–Ω—É –ø–æ–ª–Ω–æ—Å—Ç—å—é
   */
  async clearCart(): Promise<{ message: string }> {
    try {
      const response = await apiClient.delete('/cart/clear');
      return response.data;
    } catch (error: any) {
      throw new Error(error.response?.data?.message || '–û—à–∏–±–∫–∞ –æ—á–∏—Å—Ç–∫–∏ –∫–æ—Ä–∑–∏–Ω—ã');
    }
  }
}

export const cartService = CartService.getInstance();