'use client';

import React from 'react';
import { 
  AppBar, 
  Toolbar, 
  Typography, 
  Box, 
  Button,
  Container,
  Menu,
  MenuItem,
  Avatar
} from '@mui/material';
import { useRouter, usePathname } from 'next/navigation';
import RestaurantIcon from '@mui/icons-material/Restaurant';
import PersonIcon from '@mui/icons-material/Person';
import ExitToAppIcon from '@mui/icons-material/ExitToApp';
import ShoppingCartIcon from '@mui/icons-material/ShoppingCart';
import { Badge, IconButton } from '@mui/material';
import { useAuth } from '@/lib/auth-context';
import { useCart } from '@/lib/cart-context';

const Header: React.FC = () => {
  const router = useRouter();
  const pathname = usePathname();
  const { user, isAuthenticated, logout } = useAuth();
  const { getTotalItems } = useCart();
  const [anchorEl, setAnchorEl] = React.useState<null | HTMLElement>(null);

  // –ù–∞–≤–∏–≥–∞—Ü–∏–æ–Ω–Ω—ã–µ —ç–ª–µ–º–µ–Ω—Ç—ã –≤ –∑–∞–≤–∏—Å–∏–º–æ—Å—Ç–∏ –æ—Ç —Å—Ç–∞—Ç—É—Å–∞ –∞–≤—Ç–æ—Ä–∏–∑–∞—Ü–∏–∏
  const getNavigationItems = () => {
    const baseItems = [
      { label: '–ú–µ–Ω—é', path: '/' },
      { label: '–†–µ—Å—Ç–æ—Ä–∞–Ω—ã', path: '/restaurants' },
      { label: '–û –Ω–∞—Å', path: '/about' },
    ];

    if (!isAuthenticated) {
      return [
        ...baseItems,
        { label: '–í—Ö–æ–¥', path: '/auth/login' },
      ];
    }

    // –î–ª—è –∞–≤—Ç–æ—Ä–∏–∑–æ–≤–∞–Ω–Ω—ã—Ö –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª–µ–π - –¥–æ–±–∞–≤–ª—è–µ–º –ø—É–Ω–∫—Ç—ã –≤ –∑–∞–≤–∏—Å–∏–º–æ—Å—Ç–∏ –æ—Ç —Ä–æ–ª–∏
    if (user?.role === 'user') {
      return baseItems; // –ü—Ä–æ—Ñ–∏–ª—å –±—É–¥–µ—Ç –≤ –≤—ã–ø–∞–¥–∞—é—â–µ–º –º–µ–Ω—é
    }

    return baseItems; // –î–ª—è admin/manager —Ç–æ–∂–µ –±–∞–∑–æ–≤—ã–µ –ø—É–Ω–∫—Ç—ã
  };

  const handleNavigation = (path: string) => {
    router.push(path);
  };

  const handleProfileMenuOpen = (event: React.MouseEvent<HTMLElement>) => {
    setAnchorEl(event.currentTarget);
  };

  const handleProfileMenuClose = () => {
    setAnchorEl(null);
  };

  const handleProfileClick = () => {
    handleProfileMenuClose();
    router.push('/profile');
  };

  const handleLogout = async () => {
    handleProfileMenuClose();
    try {
      await logout();
      router.push('/');
    } catch (error) {
      console.error('Logout failed:', error);
      // –í—Å–µ —Ä–∞–≤–Ω–æ –ø–µ—Ä–µ–Ω–∞–ø—Ä–∞–≤–ª—è–µ–º –Ω–∞ –≥–ª–∞–≤–Ω—É—é
      router.push('/');
    }
  };

  const navigationItems = getNavigationItems();

  return (
    <AppBar position="sticky" sx={{ backgroundColor: '#2E7D32' }}>
      <Container maxWidth="lg">
        <Toolbar sx={{ justifyContent: 'space-between' }}>
          {/* –õ–æ–≥–æ—Ç–∏–ø –∏ –Ω–∞–∑–≤–∞–Ω–∏–µ */}
          <Box sx={{ display: 'flex', alignItems: 'center', gap: 1 }}>
            <RestaurantIcon sx={{ fontSize: 32 }} />
            <Typography 
              variant="h5" 
              component="div" 
              sx={{ 
                fontWeight: 'bold',
                cursor: 'pointer'
              }}
              onClick={() => handleNavigation('/')}
            >
              –ì—Ä—É–∑–∏–Ω—Å–∫–∞—è –ö—É—Ö–Ω—è
            </Typography>
          </Box>

          {/* –ù–∞–≤–∏–≥–∞—Ü–∏–æ–Ω–Ω–æ–µ –º–µ–Ω—é */}
          <Box sx={{ display: 'flex', alignItems: 'center', gap: 1 }}>
            {navigationItems.map((item) => (
              <Button
                key={item.path}
                color="inherit"
                onClick={() => handleNavigation(item.path)}
                sx={{
                  fontWeight: pathname === item.path ? 'bold' : 'normal',
                  backgroundColor: pathname === item.path ? 'rgba(255, 255, 255, 0.1)' : 'transparent',
                  '&:hover': {
                    backgroundColor: 'rgba(255, 255, 255, 0.2)',
                  },
                  borderRadius: 1,
                  px: 3,
                  py: 1,
                }}
              >
                {item.label}
              </Button>
            ))}

            {/* –ö–Ω–æ–ø–∫–∞ –∫–æ—Ä–∑–∏–Ω—ã –¥–ª—è –∞–≤—Ç–æ—Ä–∏–∑–æ–≤–∞–Ω–Ω—ã—Ö –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª–µ–π-–∫–ª–∏–µ–Ω—Ç–æ–≤ */}
            {isAuthenticated && user?.role === 'user' && (
              <IconButton
                color="inherit"
                onClick={() => handleNavigation('/cart')}
                sx={{
                  '&:hover': {
                    backgroundColor: 'rgba(255, 255, 255, 0.2)',
                  },
                }}
              >
                <Badge badgeContent={getTotalItems()} color="error">
                  <ShoppingCartIcon />
                </Badge>
              </IconButton>
            )}

            {/* –ú–µ–Ω—é –ø—Ä–æ—Ñ–∏–ª—è –¥–ª—è –∞–≤—Ç–æ—Ä–∏–∑–æ–≤–∞–Ω–Ω—ã—Ö –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª–µ–π */}
            {isAuthenticated && (
              <>
                <Button
                  color="inherit"
                  onClick={handleProfileMenuOpen}
                  startIcon={<Avatar sx={{ width: 24, height: 24, bgcolor: '#1B5E20' }}>
                    <PersonIcon sx={{ fontSize: 16 }} />
                  </Avatar>}
                  sx={{
                    borderRadius: 1,
                    px: 2,
                    py: 1,
                    '&:hover': {
                      backgroundColor: 'rgba(255, 255, 255, 0.2)',
                    },
                  }}
                >
                  {user?.first_name}
                </Button>
                
                <Menu
                  anchorEl={anchorEl}
                  open={Boolean(anchorEl)}
                  onClose={handleProfileMenuClose}
                  anchorOrigin={{
                    vertical: 'bottom',
                    horizontal: 'right',
                  }}
                  transformOrigin={{
                    vertical: 'top',
                    horizontal: 'right',
                  }}
                >
                  {user?.role === 'user' && (
                    <MenuItem onClick={handleProfileClick}>
                      <PersonIcon sx={{ mr: 1 }} />
                      –ü—Ä–æ—Ñ–∏–ª—å
                    </MenuItem>
                  )}
                  
                  <MenuItem onClick={handleLogout}>
                    <ExitToAppIcon sx={{ mr: 1 }} />
                    –í—ã—Ö–æ–¥
                  </MenuItem>
                </Menu>
              </>
            )}
          </Box>
        </Toolbar>
      </Container>
    </AppBar>
  );
};

export default Header;